<template>
  <div id="product">
    <app-header></app-header>
    <div class="product">
      <!-- v-for="(options,index) in productOptions" :key="`options_${index}`" -->
      <div class="container">
        <div class="pro-view">
          <div class="view-list">
            <img v-for="(img,index) in imgs" :key="`img_${index}`" 
            :src="img" 
            :alt="productDetail.name" 
            v-show="index==showImgIndex"/>
          </div>
          <div class="view-control">
            <div class="control-lr">
              <a href="javascript:" class="control-prev" 
              @click="showImgIndex==0?showImgIndex=imgs.length-1:showImgIndex--">上一张</a>
              <a href="javascript:" class="control-next"
               @click="showImgIndex==imgs.length-1?showImgIndex=0:showImgIndex++">下一张</a>
            </div>
            <div class="control-page">
              <a 
              v-for="(img,index) in imgs" :key="`img_${index}`"
              @click="showImgIndex=index">{{index+1}}</a>
            </div>
          </div>
        </div>
        <div class="pro-info">
          <h1 class="pro-title">{{productDetail.name}}</h1>
          <p class="pro-desc">{{productDetail.desc}}</p>
          <div class="pro-price">
            <span>
              <label class="price">{{price}}</label>元
            </span>
          </div>
          <div class="pro-choose">
            <div class="choose1">
              <p class="choose-title">
                选择
                <span class="prop-name">{{productDetail.prop1_name}}</span>
              </p>
              <ul class="choose-list clearfix">
                <li
                  :class="{active:index==indexActive1}"
                  @click="indexActive1=index,indexActive2=0"
                  v-for="(options,index) in productOptions"
                  :key="`options_${index}`"
                >
                  <a href="javascript:;">
                    <span class="list-name">{{options.prop1_value}}</span>
                    <span class="list-price">
                      <label class="price">{{options.price}}</label>元
                    </span>
                  </a>
                </li>
                <!--  -->

                <!--  -->
              </ul>
            </div>
            <!--   -->
            <div class="choose2">
              <p class="choose-title">
                选择
                <span class="prop-name"></span>
              </p>
              <ul class="choose-list clearfix">
                <li
                  v-for="(list,index) in optionsList"
                  :key="`list_${index}`"
                  :class="{active:index==indexActive2}"
                  @click="indexActive2=index"
                >
                  <a href="javascript:;">
                    <span class="list-name">{{list.prop2_value}}</span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div class="pro-btn">
            <a href="javascript:;" id="addCart" @click="joinCart">加入购物车</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import header from "../components/header";
import api from "../utils/api";
import { mapState } from "vuex";

export default {
  created() {
    api.get(`/productDetail?pid=${this.$route.query.pid}`).then(
      data => {
        document.title = data.name;
        this.productDetail = data;
        this.productOptions = data.options;
        this.imgs=this.productOptions[this.indexActive1].list[this.indexActive2].imgs;
      },
      err => {}
    );
    
  },

  updated() {
  //  console.log(this.sku_id)
  },
  data() {
    return {
      productDetail: {},
      productOptions: [],
      // optionsList:[],
      // price:'',
      imgs:[],
      // indexActive1是选择的版本
      indexActive1: 0,
      // indexActive2是选择的手机颜色
      indexActive2: 0,
      // 图片展示索引
      showImgIndex:0,
    };
  },

  computed: {
    ...mapState("user", ["isLoggedIn"]),
    optionsList() {
      // 如果productOptions是一个空数组，就会报错没有list这个属性
      return this.productOptions.length != 0
        ? this.productOptions[this.indexActive1].list
        : [];
    },
    price(){
      return this.productOptions.length != 0
        ? this.productOptions[this.indexActive1].list[this.indexActive2].price
        : "";
    },
    sku_id(){
      return this.productOptions.length != 0
        ? this.productOptions[this.indexActive1].list[this.indexActive2].sku_id
        : "";
    },
  },

  methods:{
    // joinCart点击加入购物车的回调
    joinCart(){
      if(this.isLoggedIn){
        // 调用添加购物车接口
        api.post("/user/addCart",{sku_id:this.sku_id}).then(data=>{
          // 更新总商品数量
          this.$store.dispatch("cart/CHANGEcartGoodsList");
          alert("添加成功！")
        });
      }else{
        alert("请先登录！")
      }
       
    },
  },
  watch:{
    
  },

  components: {
    // "app-header": header
  }
};
</script>

<style scoped lang="scss">
@import url(../css/iconfont.css);
@import url(../css/style.css);
@import url(../css/product.css);

</style>